<template>
  <div>
    <div>
      <div v-for="(item, index) of msgList" :key="index">
        <span>{{ item.name }}</span>
        <span>{{ item.date }}</span>
        <p>{{ item.msg }}</p>
      </div>
    </div>
    <Input v-model="msg" placeholder="请输入..." style="width: 300px" />
    <Button type="primary" @click="send">发送消息</Button>
    <Input v-model="uname" placeholder="昵称" style="width: 300px" />
  </div>
</template>

<script>
import ws from '../../web-socket';

const options = {
  code: 1,
};

export default {
  name: 'homePage',
  data() {
    return {
      msg: '',
      uname: '我是GG',
      msgList: [],
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      ws.addMessageQueue({
        options,
        callback: (data) => {
          console.log(data);
          this.msgList.push(data);
        },
      });
    },
    send() {
      ws.send({
        options,
        data: {
          msg: this.msg,
          name: this.uname,
          date: new Date().getTime(),
        },
      });
    },
  },
};
</script>

<style scoped>
</style>



